import LogUtils from './../logUtils/index';
/**
 * @description Cookie
 * @param { String } name cookie
 * @param { String } value cooke
 * @param { Number } exp  2 
 * @example
 * // nametest123451
 * setCookie('test', '12345', 60 * 60 * 1000)
 */
export function setCookie(name, value, exp = 60 * 60 * 2 * 1000) {
    const date = new Date();
    date.setTime(date.getTime() + exp);
    document.cookie = `${name}=${escape(value)};expires=${date.toUTCString()}`;
}
/**
 * @description Cookie
 * @param { String } name cookie
 * @returns { (Array | Null) } 
 * @example
 * getCookie('test')
 */
export function getCookie(name) {
    if (name) {
        const reg = new RegExp(`(^| )${name}=([^;]*)(;|$)`);
        const arr = document.cookie.match(reg);
        return arr && arr[2] ? arr[2] : null;
    }
    const getAllCookies = [];
    if (document.cookie.length) {
        const arrCookie = document
            .cookie
            .split('; ');
        for (let k in arrCookie) {
            getAllCookies.push({
                name: `${unescape(arrCookie[k].split('=')[0])}`,
                value: `${unescape(arrCookie[k].split('=')[1])}`
            });
        }
        return getAllCookies;
    }
    else {
        return null;
    }
}
/**
 * @description Cookie
 * @param { String } name cookie cookie
 * @returns { Array } 
 * @example
 * removeCookie('test')
 */
export function removeCookie(name) {
    const date = new Date();
    date.setTime(date.getTime() - 1);
    if (name) {
        const cookieInfo = getCookie(name);
        if (cookieInfo !== null) {
            document.cookie = `${name}=${cookieInfo};expires=${date.toUTCString()}`;
        }
        return;
    }
    const allCookies = getCookie();
    for (let k in allCookies) {
        document.cookie = `${allCookies[k].name}=${allCookies[k].value};expires=${date.toUTCString()}`;
    }
}
/**
 * @description num 
 * @param { Arrary } arr 
 * @param { Number } num 
 * @returns { Arrary } 
 * @example
 * getRandomDataFromArr([1,2,3,4,5,44,3,2,1,9,0,45,678], 5)
 */
export function randomDataFromArr(arr, num) {
    const newArr = Array.from(new Set(arr));
    const l = newArr.length;
    const resultArr = new Array();
    if (!(num > 0)) {
        LogUtils.logError(`0`, '[mUtils] StoreUtils randomDataFromArr => ');
        return;
    }
    if (newArr) {
        for (let i = 0; i < (num > l ? l : num); i++) {
            let index = ~~(Math.random() * newArr.length);
            resultArr.push(newArr[index]);
            newArr.splice(index, 1);
        }
        return resultArr;
    }
}
/**
 * @description  object array string undefined bool number null ...
 * @param { Any } data 
 * @example
 * checkType('1')   // string
 * @example
 * checkType({})   // object
 * @example
 * checkType([])   // array
 * @example
 * checkType(localStorage)   // storage
 */
export function checkType(data) {
    let str = Object.prototype.toString.call(data);
    return str.match(/\[object (.*?)\]/)[1].toLowerCase();
}
/**
 * @description 
 * @param { Object } obj 
 * @return { Object } 
 * @example
 * let a = {
 *   a: 1,
 *   b: 2,
 *   c: 3,
 *   d: [1, 2]
 * }
 * let b = deepClone(a)
 * a.d[0] = 3
 * console.log(a)
 * // a: {a: 1, b: 2, c: 3, d: [3, 2]}
 * console.log(b)
 * // b: {a: 1, b: 2, c: 3, d: [1, 2]}
 * // a.d[0] ab
 */
export function deepClone(obj) {
    const result = {};
    const keys = Object.keys(obj);
    let type;
    for (let k of keys) {
        type = checkType(obj[k]);
        switch (type) {
            case 'object':
                result[k] = deepClone(obj[k]);
                break;
            case 'array':
                result[k] = [].concat(obj[k]);
                break;
            default:
                result[k] = obj[k];
        }
    }
    return result;
}
/**
 * @description extend Object.assign(...arg)
 * @param { Any }   object
 * @returns { Object } 
 * @example
 * extend({a: 1}, {a: 2})   // {a: 1}
 *  Object.assign,,extend{}
 */
export function extend(...arg) {
    return deepClone(Object.assign({}, ...arg));
}
/**
 * @description 
 * @param { Arrary } arr arr
 * @return { Array } 
 * @example
 * // [1, 2, 3, undefined, "4"]
 * uniqueArray([1,2,3,3,,3,3,'4',"4",'4',])
 */
export function uniqueArray(arr) {
    return [...new Set(arr)];
}
/**
 * @description blob
 * @param { File } dataUrl  file
 * @return { Blob }   appendformdata
 */
export function dataUrlToBlob(dataUrl) {
    const arr = dataUrl.split(',');
    const mime = arr[0].match(/:(.*?);/)[1];
    const bstr = atob(arr[1]);
    let n = bstr.length;
    const u8arr = new Uint8Array(n);
    while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
    }
    return new Blob([u8arr], { type: mime });
}
/**
 * @description 
 * @param { Array } args 
 * @return { Array } 
 */
export function union(...args) {
    return Array.from(new Set([].concat(...args)));
}
/**
 * @description 
 * @param { Array } args 
 * @return { Array } 
 */
export function intersection(a, b) {
    const setB = new Set(b);
    return a.filter((item) => {
        return setB.has(item);
    });
}
/**
 * @description  --
 * @param { Array } args 
 * @return { Array } 
 */
export function diffset(a, b) {
    const setB = new Set(b);
    const setA = new Set(a);
    return [...([...a].filter((item) => {
            return !setB.has(item);
        })), ...([...b].filter((item) => {
            return !setA.has(item);
        }))];
}
/**
 * @description 
 * @param { Array | String } target 
 * @param { String | Number | ... } s   
 * @return { Number } 
 */
export function calcQuantity(target, s) {
    let newTarget = typeof target === 'string' ? target.split('') : target;
    return newTarget.reduce((t, c) => {
        return s === c ? t + 1 : t;
    }, 0);
}
