import axios from 'axios';
import { getCookie } from '../storeUtils/index';
import LogUtils from './../logUtils/index';
import { Toast, Loading } from './../feedbackUtils/index';
/**
 * axios 
 *  @param { object }params 
 *  @param { string }baseURL axios 
 *  @param { string || number }timeout 
 *  @param { object }header header 'content-type': 'application/x-www-form-urlencoded'
 *  @param { function }loadingStar loading 
 *  @param { function }loadingEnd loading loadingStar 
 *  cookietoken 
 */
class Http {
    constructor(params) {
        this.baseURL = params.baseURL;
        this.timeout = params.timeout;
        this.header = params.header;
        this.loadingStar = params.loadingStar;
        this.loadingEnd = params.loadingStar;
    }
    /**
     * 
     * @param msg
     */
    errorCreate(msg) {
        const error = new Error(msg);
        this.errorLog(error);
        throw error;
    }
    /**
     * 
     * @param error
     */
    errorLog(error) {
        // 
        if (process.env.NODE_ENV === 'development') {
            LogUtils.logError('>>>>>> Error >>>>>>', error);
        }
        // 
        Toast(error.message, 5000);
    }
    /**
     * axios axios
     * axios
     */
    createService() {
        const service = axios.create({
            baseURL: this.baseURL,
            timeout: this.timeout || 5000 // 
        });
        // 
        service.interceptors.request.use(config => {
            // 
            // const token = util.cookies.get('token')
            // token-- ['X-Token']key 
            // config.headers['X-Token'] = token
            return config;
        }, error => {
            // 
            LogUtils.logError('>>>>>> Error >>>>>>', error);
            Promise.reject(error);
        });
        // 
        service.interceptors.response.use(response => {
            // dataAxios  axios  data
            const dataAxios = response.data;
            // 
            const { code } = dataAxios;
            //  code 
            if (code === undefined) {
                //  code   D2Admin 
                return dataAxios;
            }
            else {
                //  code  
                switch (code) {
                    case 200:
                        // [  ] code === 0 
                        return dataAxios.data;
                    case -403:
                        Toast('');
                        break;
                    default:
                        //  code
                        this.errorCreate(`${dataAxios.msg}: ${response.config.url}`);
                        break;
                }
            }
        }, error => {
            if (error && error.response) {
                switch (error.response.status) {
                    case 400:
                        error.message = '';
                        break;
                    case 401:
                        error.message = '';
                        break;
                    case 403:
                        error.message = '';
                        break;
                    case 404:
                        error.message = `: ${error.response.config.url}`;
                        break;
                    case 408:
                        error.message = '';
                        break;
                    case 500:
                        error.message = '';
                        break;
                    case 501:
                        error.message = '';
                        break;
                    case 502:
                        error.message = '';
                        break;
                    case 503:
                        error.message = '';
                        break;
                    case 504:
                        error.message = '';
                        break;
                    case 505:
                        error.message = 'HTTP';
                        break;
                    default:
                        break;
                }
            }
            this.errorLog(error);
            return Promise.reject(error);
        });
        return service;
    }
    /**
     * 
     * @param params 
     * @param url url
     * @param data 
     * @param method get
     * @param isNoLoading loading
     * @return { Promise }
     */
    $http(params) {
        const service = this.createService();
        const token = getCookie('token');
        if (token)
            params.data.token = token;
        return new Promise((resolve, reject) => {
            !params.isNoLoading && this.loadingStar ?
                this.loadingStar() :
                Loading.show({
                    message: '...'
                });
            service({
                method: params.method || 'get',
                url: params.url,
                header: Object.assign({}, {
                    'content-type': 'application/x-www-form-urlencoded'
                }, this.header),
                params: params.data || {}
            }).then((res) => {
                LogUtils.group(`[mUtils] http_request  => ${params.url}`);
                LogUtils.logInfo(res, `http_request data => `);
                LogUtils.groupEnd();
                resolve(res);
            }).catch(e => {
                LogUtils.group(`[mUtils] http_request  =>`);
                LogUtils.logError(e, `http_request error => `);
                LogUtils.groupEnd();
                reject(e);
            }).finally(() => {
                this.loadingEnd ? this.loadingEnd() : Loading.clear();
            });
        });
    }
}
export default Http;
