/**
 * 
 */
import LogUtils from './../logUtils/index';
import { checkType } from './../storeUtils/index';
/**
 * @description   window.navigator.userAgent
 */
export const ua = window.navigator.userAgent;
/**
 * @description  
 */
export const EXP_MOBILE = /(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i;
/**
 * @description 
 * @return { Boolean } 
 * @example
 * isMobile() // false
 */
export function isMobile() {
    return EXP_MOBILE.test(ua);
}
/**
 * @description  IOS
 */
export const EXP_IOS = /(iPhone|iPad|iPod|iOS)/i;
/**
 * @description IOS
 * @return { Boolean } IOS
 * @example
 * isIOS() // false
 */
export function isIOS() {
    return EXP_IOS.test(ua);
}
/**
 * @description Android
 * @return { Boolean } Android
 * @example
 * isAndroid() // false
 */
export function isAndroid() {
    return (!!~ua.indexOf('Android')) ||
        (!!~ua.indexOf('Adr'));
}
/**
 * @description   /^1[3-9]\d{9}$/
 */
export const EXP_PHONE_NUM = /^1[3-9]\d{9}$/;
/**
 * @description 
 * @param { String } num  
 * @return { Boolean } true  false
 * @example
 * isPhoneNum('13651971940')   // true
 */
export function isPhoneNum(num) {
    if (typeof num !== 'string') {
        LogUtils.logError(`string: ${typeof num}`, '[mUtils] ExpUtils isPhoneNum error => ');
        return false;
    }
    return EXP_PHONE_NUM.test(num);
}
/**
 * @description  
 */
export const EXP_EMAIL = /^(\w-*\.*)+@(\w-?)+(\.\w{2,})+$/;
/**
 * @description email
 * @param { String } email  
 * @return { Boolean } true  false
 * @example
 * isEmail('185098535@qq.com')  // true
 */
export function isEmail(email) {
    return EXP_EMAIL.test(email);
}
/**
 * @description 
 * @return Boolean
 * @example
 * isWeiXin()  // true
 */
export function isWeiXin() {
    const uaLower = ua.toLowerCase();
    return String(uaLower.match(/MicroMessenger/i)) === 'micromessenger';
}
/**
 * @description 
 */
export const EXP_CHINESE = /^[\u3220-\uFA29]+$/;
/**
 * @description 
 * @param { String } str
 * @return Boolean
 * @example
 * isChinese('')  // false
 * isChinese('')   // true
 * isChinese('world')   // false
 */
export function isChinese(str) {
    return EXP_CHINESE.test(str);
}
/**
 *  Object
 */
export function isObject(o) {
    return checkType(o) === 'object';
}
/**
  * @description 
  * @param { Object } 
  * @return Boolean 
  * @example
  * let obj = {
  *   a: 1,
  *   b: 2
  * }
  * let obj1 = {}
  * isEmptyObject(obj)   // false
  * isEmptyObject(obj1)   // true
  */
export function isEmptyObject(obj) {
    if (!isObject(obj)) {
        LogUtils.logError('object', '[mUtils] ExpUtils isEmptyObject error => ');
        return false;
    }
    return Object.keys(obj).length === 0;
}
/**
 *   
 * @param str 
 * @return Boolean 
 */
export function isEmptyStr(str) {
    return str.replace(/(^\s*)|(\s*$)/g, '').length === 0;
}
